@import url(https://fonts.googleapis.com/css2?family=Libre+Caslon+Display:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;700&family=Rokkitt:wght@300;600&display=swap);
:root {
  --bg-primary-color: #ffffff;
  --bg-secondary-color: #f7faf7;
  --font-primary-color: #222222;
  --accent-green: #49784b;
  --accent-light: #a4bca5;
  --font-heading: 'Libre Caslon Display', 'serif';
}

body {
  margin: 0;
  font-family: 'Lato', 'sans-serif';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header {
  /* Position */
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  /* Box */
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* Visual */
  background-color: var(--bg-primary-color);
  /* Misc */
  z-index: 100;
}

.header__logo {
  /* Box */
  margin-left: 10px;
  /* Typogrpahy */
  font-family: var(--font-heading);
  font-weight: 400;

  font-size: 16px;
  text-decoration: none;
  color: var(--font-primary-color);

  /* Misc */
  cursor: pointer;
}

.header__basket {
  /* Position */
  /* Box */
  display: flex;
  align-items: center;
  margin-right: 10px;
  /* Visual */
  color: var(--font-primary-color);
  /* Misc */
  cursor: pointer;
}

.header__basket__count {
  /* Position */
  /* Box */
  display: flex;
  align-items: center;
  justify-content: center;
  width: 15px;
  height: 15px;
  margin-right: 5px;
  padding: 6px;
  /* Typogrpahy */
  text-decoration: none;
  font-weight: 600;

  color: var(--bg-primary-color);
  /* Visual */
  background-color: var(--accent-green);
  border-radius: 50%;
  /* Misc */
}

/* Position */
/* Box */
/* Typogrpahy */
/* Visual */
/* Misc */

.home {
  /* Box */
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 30px;
}

.filter__container {
  /* Position */
  /* Box */
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 5px;
  padding: 0 10px;
  /* Typogrpahy */
  color: var(--font-primary-color);
  /* Visual */

  /* Misc */
}

@media (min-width: 769px) {
  .filter__container {
    justify-content: space-between;
    margin-right: 50px;
  }
}

.drop__items {
  display: flex;
  justify-content: center;
  align-items: center;
}

.filter,
.sort {
  cursor: pointer;
  margin: 0 10px;
}

.filter__item {
  display: flex;
  align-items: center;
  justify-content: center;
}

.filter__icon {
  transition: 0.5s ease-in;
}

.filter__icon.rotate {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.filter__reset {
  /* Box */
  height: 25px;
  padding: 5px 10px;
  margin-left: 10px;
  margin-right: 5px;
  /* Typogrpahy */
  font-size: 14px;
  /* Visual */
  border: none;
  border-radius: 10px;
  background-color: var(--accent-green);
  color: var(--bg-primary-color);
  /* Misc */
  cursor: pointer;
}

.product__container {
  /* Position */
  /* Box */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-left: 15px;
  margin-right: 15px;
  /* Typogrpahy */
  /* Visual */
  /* Misc */
}

@media (min-width: 769px) {
  .product__container {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.search__input {
  /* Position */
  /* Box */
  width: 115px;
  /* Typogrpahy */
  /* Visual */
  /* Misc */
}

.search__input {
  /* Box */
  margin-left: 5px;
  padding: 5px;
  /* Typogrpahy */
  font-family: inherit;
  font-style: italic;
  /* Visual */
  outline: none;
  border-radius: 5px;
  border: solid 1px gray;
  /* Misc */
}

.search__input:focus {
  outline: none;
}

/* Position */
/* Box */
/* Typogrpahy */
/* Visual */
/* Misc */

.product {
  /* Box */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-self: flex-end;
  margin: 15px;
  padding: 10px;
  max-height: 400px;
  min-width: 100px;
  max-width: 350px;
  /* Visual */
  background-color: var(--bg-secondary-color);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
}

.product__info {
  height: 100px;
  margin-bottom: 15px;
  text-align: center;
}
.product__price {
  margin-top: 5px;
  text-align: start;
}

.product > img {
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}

.product__btn {
  /* Box */
  margin-top: 10px;
  padding: 10px 20px;
  /* Typogrpahy */
  font-size: 16px;
  /* Visual */
  color: var(--bg-primary-color);
  background-color: var(--accent-green);
  border: none;
  border-radius: 10px;
  /* Misc */
  cursor: pointer;
}

.product__btn:hover {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

/* Position */
/* Box */
/* Typogrpahy */
/* Visual */
/* Misc */

.filter__form__container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
  height: 50px;
}

@media (max-width: 769px) {
  .filter__form__container {
    /* Box */
    padding-top: 10px;
    justify-content: center;
    /* Visual */
  }
}

.filter__form {
  /* Box */
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
}

.filter__form label {
  height: 0;
}

.filter__form select {
  /* Box */
  padding: 5px 10px !important;
  /* Typogrpahy */
  font-weight: bold;
  /* Visual */
  border-radius: 5px;
  background-color: var(--bg-primary-color) !important;
}

.category {
  /* Box */
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 10px;
}

.checkoutProduct {
  display: flex;
  margin-top: 20px;
  margin-bottom: 20px;
}

.checkoutProduct-info {
  padding-left: 20px;
}

.checkoutProduct-info > button {
  /* Box */
  margin-top: 10px;
  padding: 10px 20px;
  /* Typogrpahy */
  font-size: 16px;
  /* Visual */
  color: var(--bg-primary-color);
  background-color: var(--accent-green);
  border: none;
  border-radius: 10px;
  /* Misc */
  cursor: pointer;
}

.checkoutProduct-info > button:hover {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.checkoutProduct-image {
  object-fit: contain;
  width: 180px;
  height: 180px;
}

.checkoutProduct-rating {
  display: flex;
}

.checkoutProduct-title {
  font-size: 17px;
  font-weight: 800;
}

.subtotal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 300px;
  height: 100px;
  padding: 20px;
  background-color: var(--bg-secondary-color);
  border: 1px solid #dddddd;
  border-radius: 3px;
}

@media (min-width: 769px) {
  .subtotal {
    position: fixed;
    top: 110px;
    right: 110px;
  }
}

.subtotal > button {
  /* Box */
  margin-top: 10px;
  padding: 10px 20px;
  /* Typogrpahy */
  font-size: 16px;
  /* Visual */
  color: var(--bg-primary-color);
  background-color: var(--accent-green);
  border: none;
  border-radius: 10px;
  /* Misc */
  cursor: pointer;
}

.checkout {
  display: flex;
  flex-direction: column;
  margin: 15px;
  padding: 20px;
  background-color: white;
  height: -webkit-max-content;
  height: max-content;
}

@media (min-width: 769px) {
  .checkout {
    flex-direction: row-reverse;
    justify-content: space-between;
  }
}

.checkout__title {
  padding: 10px;
  text-align: center;
}

@media (min-width: 769px) {
  .checkout__title {
    text-align: start;
    margin-left: 30px;
  }
}

